.portfolio-animation-enter{
    animation: 0.5s ease-out 0s slideInFromRight;
}

.portfolio-animation-exit{
    animation: 0.2s ease-out 0s slideOutFromRight;
    animation-fill-mode: forwards;
}

@keyframes slideInFromRight {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(0);
    }
}

@keyframes slideOutFromRight {
    0% {
        transform: translateX(0%);
        opacity: 50%;
    }
    100% {
        transform: translateX(100%);
        opacity: 0%;
    }
}
