* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}

@media (min-width: 768px) {
  .col-left, .col-right{
    height: 103vh;
  }
  
  .scrollable-area{
    overflow: scroll;
  }

  *{
    overflow: hidden;
  }

  body{
    width: 100.1vw;
  }
}

.loading{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
